.ButtonWrapper {
    display: flex;
    justify-content: center;  
    margin-bottom: 12px;
  }
  
  .Button {
    display: inline-block;  
    min-width: 180px;
    padding: 8px 16px;
    border-radius: 2px;
    border: 0;
  
    color: #fff;
    background-color: #02be6e;
    box-shadow: 0px 3px 1px 3px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  
    text-align: center;
    text-decoration: none;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
  
    opacity: 1;
  
    transition: opacity 350ms cubic-bezier(0.4, 0, 0.2, 1), background-color 350ms linear;
  
    cursor: pointer;
  
     
  }

  .Button:hover,
  .Button:focus {
    opacity: 0.8;
    background-color: #299667;
  }